function addColor(url, color) {
    const encodedColor = color.replace('#', '%23');
    return url.replace('%3e', ` fill=\'${encodedColor}\' stroke=\'${encodedColor}\'%3e`);
}

class PopupPlugin {

    constructor(options) {
        const me = this;

        me.id = 'popup';
        me.name = {
            en: 'Popup',
            ja: 'ãããã¢ãã'
        };
        me.iconStyle = {
            backgroundSize: '32px',
            backgroundImage: `url("${addColor(popupSVG, 'white')}")`
        };
        me.enabled = false;
        me._onMouseDown = me._onMouseDown.bind(me);
        me._onMouseCancel = me._onMouseCancel.bind(me);
    }

    onAdd(map) {
        this.map = map;
    }

    onEnabled() {
        const me = this,
            {map} = me;

        map.on('mousedown', me._onMouseDown);
        map.on('touchstart', me._onMouseDown);
        map.on('mouseup', me._onMouseCancel);
        map.on('touchend', me._onMouseCancel);
        map.on('drag', me._onMouseCancel);
        map.on('rotate', me._onMouseCancel);

        me.client = new WebPubSubClient(`${PUBSUB_URL}?access_token=${PUBSUB_ACCESS_TOKEN}`);
        me.client.on('group-message', e => {
            const popup = new Popup({
                className: 'popup-text',
                closeButton: false,
                closeOnClick: false
            })
                .setLngLat(e.message.data.center)
                .setHTML([
                    e.message.data.text
                ].join(''))
                .addTo(map.map);
            me.popups.push(popup);
        });
        me.client.start();
        me.client.joinGroup('group1');
        me.popups = [];
    }

    onDisabled() {
        const me = this,
            {map} = me;

        map.off('mousedown', me._onMouseDown);
        map.off('touchstart', me._onMouseDown);
        map.off('mouseup', me._onMouseCancel);
        map.off('touchend', me._onMouseCancel);
        map.off('drag', me._onMouseCancel);
        map.off('rotate', me._onMouseCancel);

        me.client.stop();
        me.popups.forEach(popup => {
            popup.remove();
        });
    }

    _onMouseDown(e) {
        const me = this,
            {map} = me;

        me.timeout = setTimeout(() => {
            const popup = new Popup({
                    className: 'popup-input',
                    closeOnClick: false
                })
                    .setLngLat(e.lngLat)
                    .setHTML([
                        '<input id="text-input" class="text-input" type="text" placeholder="ã³ã¡ã³ããå¥å">',
                        '<br>',
                        '<button id="send-button" class="send-button" disabled>éä¿¡</button>'
                    ].join(''))
                    .addTo(map.map),
                inputElement = map.container.querySelector('#text-input'),
                buttonElement = map.container.querySelector('#send-button');

            inputElement.addEventListener('input', () => {
                buttonElement.disabled = !inputElement.value.match(/\S/);
            });
            buttonElement.addEventListener('click', () => {
                console.log({
                    text: inputElement.value,
                    center: e.lngLat
                });
                me.client.sendToGroup('group1', {
                    text: inputElement.value,
                    center: e.lngLat
                }, 'json');
                popup.remove();
            });
            if (me.inputPopup) {
                me.inputPopup.remove();
            }
            me.inputPopup = popup;
        }, 1000);
    }

    _onMouseCancel() {
        const me = this;

        if (me.timeout) {
            clearTimeout(me.timeout);
        }
    }

}

export default function() {
    return new PopupPlugin();
}
